body {
    margin: 0;
    padding: 0;
    overflow: hidden;
  }

.login-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

label {
  padding-bottom: 5rem;
}

input[type=password] {
  margin: 10px;
  padding: 10px;
  font-size: 20px;
  border-radius: 20px pink;
  outline: none;
  text-align: center;
}

